                        </svg>
                    </div>
                    <p class="notice">公告:{{head.promotion_info}}</p>
                </div>
             </div>
             <div class='activity' v-if="bol">
                    <div class="activity1">
                        <span class="minus">{{head.activities[0].icon_name}}</span>
                        <span class="minuse">{{head.activities[0].description}}(APP专享)</span>
                    </div>
                    <div class="activity2">
                        <span class="minues">{{head.activities.length}}个活动</span>
                        <svg t="1566977412998" class="icon " viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="4947" width="200" height="200">
                            <path d="M713.892234 598.646154c-23.630769 0-43.323077-7.876923-59.076923-27.569231L249.153772 137.846154C217.64608 106.338462 221.584542 55.138462 253.092234 23.630769c31.507692-31.507692 82.707692-27.569231 114.215384 3.938462l401.723077 437.169231c31.507692 31.507692 27.569231 82.707692-3.938461 114.215384-11.815385 11.815385-31.507692 19.692308-51.2 19.692308z" fill="white" p-id="4948"></path>
                            <path d="M312.169157 1024c-19.692308 0-39.384615-7.876923-55.138462-23.630769-31.507692-31.507692-35.446154-82.707692-3.938461-114.215385l401.723077-425.353846c31.507692-31.507692 82.707692-35.446154 114.215384-3.938462 31.507692 31.507692 35.446154 82.707692 3.938462 114.215385L371.24608 1000.369231c-15.753846 15.753846-39.384615 23.630769-59.076923 23.630769z" fill="white" p-id="4949"></path>
                        </svg>
                    </div>
             </div>
             <!-- <router-link tag='i' class="el-icon-arrow-left" to='/Home'></router-link> -->
             <i class="el-icon-arrow-left" @click="$router.go(-1)"></i>
         </div>
         
        </div>
         <div class="tabs ta1" :class="{'ta2':bol}">
                 <div class="tabs1"><p @click="getsty()" :class="{'psty':sty}">商品</p></div>
                 <div class="tabs1"><p @click="getsty1()" :class="{'psty':!sty}">评价</p></div>
          </div>
         <div class="content " :class="{'con1':bol}">
                <div class="shoptabs-left" >
                    <ul class="shop14">
                      <li ref='shopleft' class="shopac" v-for='(item,index) of menu' :key='index' @click="getup(index)" :class="{'active':index==act}" >{{item.name}}</li>
                    </ul>
                </div>
                <div class="shoptabs-right" ref='shoptabsright' @touchmove='getshop()'>
                    <ul>
                      <li v-for='(item,index) of menu' :key="index" class="shoplists" ref="shoplist">
                          <div class="shophead"><div><b>{{item.name}}</b><span>{{item.description}}</span></div><p>···</p></div>
                          <div class="shopcon" v-for='(i,ab) of item.foods' :key='ab' ref='shopfood'>
                              <div class="shopcon-head">
                                  <img :src="`//elm.cangdu.org/img/${i.image_path}`">
                                  <div class="shopcon-right">
                                      <p class="shopcon-title">{{i.name}}</p>
                                      <p class="description">{{i.description}}</p>
                                      <p class="rating_count"><span>月售{{i.month_sales}}</span><span>好评率{{i.satisfy_rate}}%</span></p>
                                      <p class="activity"  v-if="i.activity!=null"><span>{{i.activity.image_text}}</span></p>
                                  </div>
                              </div>
                              <div class="choice">
                                <p class="price">￥<span v-if='i.specfoods.length!=0'>{{i.specfoods[0].price}}</span><span v-if='i.specfoods.length>1'>起</span></p>
                                <div class="operation">
                                  <p class="addpend1" v-if='i.is_featured>0&&i.specfoods.length<=1' @click="getsub(index,ab)">-</p>
                                  <p class="addpend1" v-if='i.is_featured>0&&i.specfoods.length>1' @click="getsub1(index,ab)">-</p>
                                  <p class="oper-num" v-if='i.is_essential'>{{i.is_featured}}</p>
                                  <p class="btn" v-if='i.specfoods.length>1' @click="getsh(index,ab)">选规格</p>
                                  <p class="addpend" v-if='i.specfoods.length<=1' @click="getadd(index,ab,$event)">+</p>
                                </div>
                              </div>
                              <div class="newshop" v-if="i.attributes.length>0" >新品</div>
                          </div>
                      </li>
                    </ul>
                </div>
         </div>
          <div class="shopfoot">
            <div class="footer">
              <van-button type="primary" @click="showPopup" >
                  <div  :class="{'shopcars1':this.$store.state.shop.length>0} " class="shopcars">
                 <svg t="1567915495402" class="icon" viewBox="0 0 1228 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="9038" width="200" height="200">
                    <path d="M265.503702 938.888659c0 46.957981 40.269199 85.043088 89.820789 85.043088 49.551591 0 89.752537-38.085107 89.752537-85.043088 0-47.026234-40.269199-85.111341-89.752537-85.111341-49.619843 0-89.82079 38.085107-89.820789 85.111341zM804.018922 938.888659c0 46.957981 40.269199 85.043088 89.820789 85.043088 49.551591 0 89.752537-38.085107 89.752537-85.043088 0-47.026234-40.200946-85.111341-89.752537-85.111341s-89.752537 38.085107-89.752537 85.111341zM1073.344784 683.62289H304.066579c-12.353771 0-33.443911-20.885381-41.22474-46.275453L89.547778 31.739637A45.046901 45.046901 0 0 0 34.467704 1.776622c-23.888508 6.142759-38.016854 29.553497-31.53283 52.213453L176.433694 660.212152c16.312438 53.919775 65.386259 108.522079 127.632885 108.522078h769.278205c24.844048 0 44.910395-19.110806 44.910395-42.589797s-20.066347-42.521544-44.910395-42.521543z" fill="#ffffff" p-id="9039"></path>
                    <path d="M1209.099762 121.765185a91.458859 91.458859 0 0 0-71.665524-33.785176H291.166784a91.458859 91.458859 0 0 0-71.665523 33.785176 81.562191 81.562191 0 0 0-14.606116 74.66865l102.584078 340.377111c10.988714 36.515291 46.207199 61.700603 86.271639 61.700603h641.167551c40.06444 0 75.282926-25.253565 86.27164-61.700603l102.584078-340.377111a81.562191 81.562191 0 0 0-14.674369-74.66865z" fill="#ffffff" p-id="9040"></path>
                 </svg>
                 <div class="Num" v-if="this.$store.state.shop.length>0">{{Num}}</div>
              </div>
              <div class="total-pirce" >
                  <span class="s1">￥{{nums}}</span><br />
                  <span class="s2">{{htm}}</span>
              </div>
              </van-button>
              <van-popup v-model="show" position="bottom" :style="{ height: '20%',bottom:'0.94rem' }" z-index='0'>
                <div class="cartshow" >
                   <div class="carttitle">
                    <div class="title-name">购物车</div>
                    <div class="datecart">
                        <svg t="1568077902276" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="2073" width="200" height="200"><path d="M715.280148 1023.90001H303.72034c-79.692218 0-144.48589-64.793672-144.48589-144.48589V352.86554c0-14.898545 12.098818-28.197246 26.897373-28.197246s26.897373 10.798945 26.897373 25.697491v529.048335c0 49.995118 40.596036 90.591153 90.591154 90.591153h411.559808c49.995118 0 90.591153-40.696026 90.591153-90.591153V350.365785c0-14.898545 12.098818-26.897373 26.897374-26.897374 14.898545 0 26.897373 12.098818 26.897373 26.897374v529.048335c0.19998 79.692218-64.593692 144.48589-144.28591 144.48589zM791.872669 267.87384l-47.495362-25.49751c4.39957-8.29919 7.599258-16.698369 7.599258-49.495166v-62.193926c0-32.796797-3.199688-41.195977-7.599258-49.495167-4.499561-8.49917-11.098916-15.098526-19.498096-19.598086-8.39918-4.39957-16.79836-7.599258-49.595157-7.599258H343.716434c-32.796797 0-41.195977 3.199688-49.595157 7.599258-8.39918 4.499561-14.998535 11.098916-19.498096 19.498096-4.39957 8.29919-7.599258 16.698369-7.599258 49.495166v62.193927c0 32.796797 3.199688 41.195977 7.599258 49.495166l-47.495361 25.49751c-10.498975-19.498096-13.998633-38.39625-13.998633-74.992676v-62.193927c0-36.596426 3.499658-55.494581 13.998633-74.992676 9.599063-17.998242 23.597696-31.996875 41.595937-41.595938 19.498096-10.398984 38.496241-13.998633 74.892687-13.998633h331.66761c36.496436 0 55.39459 3.499658 74.892687 13.998633 17.998242 9.599063 31.996875 23.597696 41.595937 41.595938 10.498975 19.498096 13.998633 38.39625 13.998633 74.992676v62.193927c0.09999 36.596426-3.399668 55.494581-13.898642 75.092666z" fill="#dbdbdb" p-id="2074"></path>
                            <path d="M132.337076 188.681574h754.426326c29.79709 0 53.894737 24.097647 53.894737 53.894737s-24.097647 53.894737-53.894737 53.894737H132.337076c-29.79709 0-53.894737-24.097647-53.894736-53.894737s24.097647-53.894737 53.894736-53.894737z" fill="#dbdbdb" p-id="2075"></path><path d="M374.813397 404.260521c14.898545 0 26.897373 12.098818 26.897373 26.897374v431.057904c0 14.898545-12.098818 26.897373-26.897373 26.897374-14.898545 0-26.897373-12.098818-26.897373-26.897374V431.157895c-0.09999-14.798555 11.998828-26.897373 26.897373-26.897374z" fill="#dbdbdb" p-id="2076"></path><path d="M644.187091 404.260521c-14.898545 0-26.897373 12.098818-26.897373 26.897374v431.057904c0 14.898545 12.098818 26.897373 26.897373 26.897374s26.897373-12.098818 26.897373-26.897374V431.157895c0.09999-14.798555-11.998828-26.897373-26.897373-26.897374z" fill="#dbdbdb" p-id="2077"></path>
                        </svg>
                        <p>清空</p>
                    </div>
                   </div>
                   <div class="cartcon" >
                      <div class="main-cart" v-for="(item,index) of this.$store.state.shop" :key='index'>
                            <div class="cart-name"><p>{{item.name}}</p><p>{{item.type}}</p></div>
                            <p class="cart-pirce"><span>￥</span>{{item.pirce}}</p>
                            <div class="cart-operate">
                                  <p class="cart-jian" @click="jian(index,item)">-</p>
                                  <p class="cart-num">{{item.num}}</p>
                                  <p class="cart-jia" @click="jia(index,item)">+</p>
                            </div>
                      </div>
                   </div>
                </div>
               
              </van-popup>
  
              <div class="start" v-if="this.nums<this.head.float_minimum_order_amount">还差￥{{this.head.float_minimum_order_amount-this.nums}}起送</div>
              <div class="start start1" v-else >去结算</div>
            </div>
          </div>
          <transition name='fades'> 
          <div class="model" v-if="subbool"></div>
          </transition>
          <transition name='fades'>
           <div class="shmodel" v-if="subbool" >
             <div class="p1"><p class="s1">{{typename}}</p><p class="s2" @click="getmo()">x</p></div>
             <div class="p2">
               <p>规格</p>
               <div>
                 <button :class="{'btn1':index==n}" v-for='(item,index) of shopes' :key='index' @click="getbtn(index)">{{item.specs_name}}</button>
               </div>
             </div>
              <footer>
                  <p class="s1"><span>￥</span><b>{{shopes[n].price}}</b></p>
                  <button @click="getca()">加入购物车</button>
               </footer>
           </div>
           </transition>
           <transition name='fades'>
             <div v-if="subbol" class="wran1">多规格商品只能去购物车删除哦</div>
           </transition>
    </div>
</template>

<script>
import jq from "jquery";
import './../theBall.css'
import ball from './../theBall.js'
export default {
  data() {
    return {
      head: {},
      uid: 0,
      htm: "",
      bol: false,
      sty: true,
      activeKey: 0,
      menu: [],
      act: 0,
      listhight: [],
      typename: "",
      subbool: false,
      shopes: [],
      btnbo: false,
      n: 0,
      nm: 0,
      in: 0,
      in1: 0,
      subbol: false,
      show: false
    };
  },

  mounted() {
    //接收传递的id
    this.uid = this.$route.query.id;
    //通过接口利用主页路由传来的ID值获取到相应的数据，渲染出来
    var that = this;
    this.http
      .get(
        "http://elm.cangdu.org/shopping/restaurant/" +
          this.uid +
          "?latitude=46.02117&longitude=128.87285"
      )
      .then(res => {
        // console.log(res);
        that.head = res;
        if (this.head.piecewise_agent_fee.tips != undefined) {
          this.htm = this.head.piecewise_agent_fee.tips;
        } else {
          this.htm = "";
        }
        if (this.head.activities.length == 0) {
          this.bol = false;
        } else {
          this.bol = true;
        }
      });
    //获取菜单栏的数据
    this.http
      .get("http://elm.cangdu.org/shopping/v2/menu?restaurant_id=" + this.uid)
      .then(res => {
        that.menu = res;
        // console.log(that.menu);
        this.$nextTick(() => {
          this.getlist();
        });
        that.$store.state.shop.map(item => {
          that.menu[item.in].foods[item.in1].is_featured = item.num;
          that.menu[item.in].foods[item.in1].is_essential = true;
        });
      });
  },
  computed: {
    nums() {
      return this.$store.getters.nums;
    },
    Num() {
      return this.$store.getters.Num;
    }
  },
  methods: {
    showPopup() {
      if (this.$store.state.shop.length > 0) {
        this.show = !this.show;
      }
    },
    getbtn(index) {
      this.n = index;
    },
    //取消模态框
    getmo() {
      this.subbool = false;
    },
    //选择规格
    getsh(index, ab) {
      this.typename = this.menu[index].foods[ab].name;
      this.subbool = true;
      this.shopes = this.menu[index].foods[ab].specfoods;
      this.nm = this.menu[index].foods[ab].is_featured;
      this.in = index;
      this.in1 = ab;
    },
    //切换商品和评价的选项卡效果
    getsty() {
      this.sty = true;
    },
    getsty1() {
      this.sty = false;
    },
    //点击左侧侧边栏，使右边的内容随着左侧的选项进行滚动
    getup(index) {
      this.act = index;
      // console.log(this.$refs.shoptabsright.scrollTop)
      // this.$refs.shoptabsright.scrollTop='5000'
      // console.log(jq(".shoptabs-right").animate)
      // console.log(this.listhight[index])
      jq(".shoptabs-right").animate({ scrollTop: this.listhight[this.act] });
      // this.listhight[index]
    },
    getlist() {
      //获取每一个li
      let slist = this.$refs.shoplist;
      // console.log(slist);
      //把第一个li的高度放入数组中
      var height = 0;
      this.listhight.push(height);
      for (var i = 0; i < slist.length; i++) {
        // console.log(slist[i].clientHeight)
        height += slist[i].clientHeight;
        // console.log(height)
        this.listhight.push(height);
      }
      // console.log(this.listhight)
    },
    getshop() {
      //获取li与顶端的距离
      // console.log(this.act)
      let scrollY = this.$refs.shoptabsright.scrollTop;
      // console.log(scrollY)
      //循环上面获取每一个li高度的数组
      for (var i = 0; i < this.listhight.length; i++) {
        //判断当下标处于height1和height2之间的时候显示
        let height1 = this.listhight[i];
        let height2 = this.listhight[i + 1];
        // console.log('height1:'+height1+','+'height2:'+height2)
        if (!height2 || (scrollY >= height1 && scrollY < height2)) {
          this.act = i;
          // console.log(this.act)
          jq(".shop14").animate(
            {
              scrollTop: this.act * this.$refs.shopleft[this.act].clientHeight
            },
            100
          );
          return;
        }
      }

      this.act = 0;
    },
    //点击+号加入购物车
    getadd(index, ab,event) {
      //点击加号让购物车抖动
      ++this.menu[index].foods[ab].is_featured;
      this.menu[index].foods[ab].is_essential = true;
      var obj = {};
      obj.name = this.menu[index].foods[ab].name;
      obj.pirce = this.menu[index].foods[ab].specfoods[0].price;
      obj.num = this.menu[index].foods[ab].is_featured;
      obj.type = "";
      obj.id = this.menu[index].foods[ab].specfoods[0].food_id;
      obj.in = index;
      obj.in1 = ab;
      this.$store.commit("jia", obj);
      for (var i in this.$store.state.shop) {
        if (
          this.$store.state.shop[i].id ==
          this.menu[index].foods[ab].specfoods[0].food_id
        ) {
          this.menu[index].foods[ab].is_featured = this.$store.state.shop[
            i
          ].num;
        }
      }
      // console.log(this.menu[index].foods[ab].is_featured);
      //点击加号时,创建一个小球
    //   let div = document.createElement('div')
    //   div.className='ballfly'
    //   document.body.appendChild(div)
    //   let top = event.clientY
    //   let left = event.clientX
    //   div.style.top = top+'px'
    //   div.style.left = left+'px'
    //   let y = (window.innerHeight-top)
    //   console.log(y)
    //   div.style.transform=`translateY(${y}px)`
    //   div.style.transform=`translateX(-${left-10}px)`
    //   console.log(top)
    ball(event)
    setTimeout(()=>{
      document.querySelector(".shopcars").classList.add("shopcares");
      setTimeout(()=>{
        document.querySelector(".shopcars").classList.remove("shopcares");
      },400)
    },600)
      
    //  console.log(event)
    
      
    },
    //点击减号减少数量]
    getsub(index, ab) {
      for (var i in this.$store.state.shop) {
        if (
          this.$store.state.shop[i].id ==
          this.menu[index].foods[ab].specfoods[0].food_id
        ) {
          this.menu[index].foods[ab].is_featured = this.$store.state.shop[
            i
          ].num;
        }
      }
      this.menu[index].foods[ab].is_featured--;

      if (this.menu[index].foods[ab].is_featured < 1) {
        this.menu[index].foods[ab].is_featured = 0;
        this.menu[index].foods[ab].is_essential = false;
      }
      var obj = {};
      obj.in = index;
      obj.in1 = ab;
      obj.name = this.menu[index].foods[ab].name;
      obj.pirce = this.menu[index].foods[ab].specfoods[0].price;
      obj.num = this.menu[index].foods[ab].is_featured;
      obj.type = "";
      obj.id = this.menu[index].foods[ab].specfoods[0].food_id;
      this.$store.commit("jian", obj);
    },
    getsub1(index, ab) {
      this.subbol = true;
    },
    getca() {
      ++this.menu[this.in].foods[this.in1].is_featured;
      this.menu[this.in].foods[this.in1].is_essential = true;
      var obj = {};
      obj.name = this.typename;
      obj.pirce = this.shopes[this.n].price;
      obj.num = ++this.nm;
      obj.type = this.shopes[this.n].specs_name;
      obj.id = this.shopes[this.n].food_id;
      this.$store.commit("jia", obj);
      // console.log(this.shopes[this.n].specs_name);
      this.subbool = false;
      for (var i in this.$store.state.shop) {
        if (
          this.$store.state.shop[i].id ==
          this.menu[this.in].foods[this.in1].specfoods[i].food_id
        ) {
          this.menu[this.in].foods[
            this.in1
          ].is_featured = this.$store.state.shop[i].num;
        }
      }
    },
    //购物车里的+-效果
    jia(index, item) {
      this.$store.commit("cartjia", index);
      this.menu[item.in].foods[item.in1].is_featured++;
    },
    jian(index, item) {
      this.$store.commit("cartjian", index);
      this.menu[item.in].foods[item.in1].is_featured--;
      if (this.menu[item.in].foods[item.in1].is_featured < 1) {
        this.menu[item.in].foods[item.in1].is_essential = false;
      }
      if (this.$store.state.shop.length < 1) {
        this.show = false;
      }
    }
  },
  watch: {
    subbol: function() {
      if (this.subbol == true) {
        setTimeout(() => {
          return (this.subbol = false);
        }, 2000);
      }
    }
  }
};
</script>

